import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import { Card, Container, FloatingLabel, Form } from 'react-bootstrap';
import ReactMarkdown from 'react-markdown';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      input: '# H1 Heading  \n## H2 Heading  \n[FCC](https://www.freecodecamp.org)  \nSome `inline` code.  \n```\nSome\ncode\nblock.\n```  \n1. This\n2. is\n3. an\n4. ordered\n5. list  \n> This is a  \n>Blockquote.  \n![Labrador puppy](./markdown-previewer/labrador-puppy.jpg "Labrador puppy")  \nThis image shows a **Labrador puppy**.',
    };

    this.handleTextInput = this.handleTextInput.bind(this);
  }

  handleTextInput(event) {
    this.setState({
      input: event.target.value,
    });
  }

  render() {
    return (
      <Container>
        <Card style={{ marginTop: '20px', marginBottom: '20px' }}>
          <Card.Body>
            <Card.Title>Markdown Previewer</Card.Title>
            <FloatingLabel label='Editor'>
              <Form.Control
                id='editor'
                as='textarea'
                placeholder='Edit your code'
                style={{ height: '300px' }}
                value={this.state.input}
                onChange={this.handleTextInput}
              />
            </FloatingLabel>
            <Card.Title style={{ marginTop: '20px' }}>Preview</Card.Title>
            <div id='preview'>
              <ReactMarkdown>
                {this.state.input}
              </ReactMarkdown>
            </div>
          </Card.Body>
        </Card>
      </Container>
    );
  }
}

export default App;
